<template>
  <div v-if="NowInfo"
       style="background-color:#f6f6f6;font-size:15px; padding:20px 0;position:fixed;top:50px;bottom:0px;left:0px;right:0px;overflow-y:scroll;">
    <!-- 个人信息 -->
    <section>
      <div class="headUrl">
        <img v-if="NowInfo.order"
             :src="NowInfo.order.headUrl"
             alt />
        <img v-else
             src="https://wx.qlogo.cn/mmopen/vi_32/9fcFhCxx3sxFwOtNJTsfkn2Us0pDbFv4f88ThciaCRHoSnTjHqXM35USNwv7LLBZliceVvMic8wx9lQkDNNfWEDqA/132"
             alt />
        <div class="headUrl_fo">
          <div class="name">
            <p>{{ $route.query.cooperationName || '暂无' }}</p>
            <p class="c_88 line">{{ $route.query.scaleName || '暂无' }}-{{ $route.query.roomSn }}</p>
            <p class="c_88 line">到期时间：
              <span style="color:#000;font-weight:bold;">{{ NowInfo.finishTime | dataFilter('YYYY.MM.DD') }}</span>
            </p>
          </div>
          <div @click="goDetail"
               class="img_right">
            <span class="c_88">租赁详情</span>
            <img src="@/assets/Chevron.png"
                 alt />
          </div>
        </div>
      </div>
    </section>
    <!-- 查询生活，房费账单 -->
    <div v-if="NowInfo.status == '1' || NowInfo.status == '2'"
         class="add_btn"
         @click="show_addType = true">
      <van-icon name="add"
                color='#FF7F47'
                size='18px'
                style="padding-right:10px;" />
      <div>添加账单</div>
    </div>
    <section>
      <div id="roomBox">
        <div class="room_fo"
             v-for="(item,i) in allInfo"
             :key="i">
          <div class="loop"
               :class="item.payStatus == 1?'payed':'nopay'">
            <div class="loop_l"
                 @click="goRoom(item)">
              <div>
                <p style="white-space:nowrap;">
                  <span v-if="NowInfo.orderType == '3'">
                    {{ item.startTime | dataFilter('YYYY.MM.DD') }}~{{ item.endTime | dataFilter('YYYY.MM.DD') }}
                  </span>
                  <span v-if="NowInfo.orderType == '4'">
                    {{ item.new_endTime ? item.new_endTime : "" }}
                  </span>
                  <span v-if="!item.set_life"
                        class="w16"
                        :class="item.payStatus == '1'?'ffa':'ff7'">租金</span>
                  <span v-else
                        class="w16"
                        :class="item.payStatus == '1'?'edf':'bc'">生活</span>
                </p>
                <p v-if="item.diff > '0' && item.payStatus == '0'"
                   class="f12">距收款日：{{Math.abs(item.diff)}}天</p>
                <p v-else-if="item.diff < '0' && item.payStatus == '0'"
                   class="c_f0 f12">已逾期{{Math.abs(item.diff)}}天</p>
                <p v-else-if="item.diff == '0' && item.payStatus == '0'"
                   class="f12">今日到期</p>
                <p v-else-if="item.payStatus == '1'"
                   class="f12">到账日期：{{ item.payTime?item.payTime.substr(0,10):item.payTime }}</p>
              </div>
              <img v-if="item.payStatus == '1'"
                   class="ispayed"
                   src="@/assets/order/payed.png"
                   alt />
            </div>
            <p class="money">&yen;{{ item.set_life?item.totalPrice:item.roomPrice }}</p>
          </div>
        </div>
      </div>
    </section>
    <!-- 账单类型 -->
    <van-popup v-model='show_addType'
               position="bottom"
               class="type_bottom">
      <div style="font-weight:bold;"
           class="bb">请选择账单类型</div>
      <div class="bb"
           v-if="NowInfo.orderType == '3'"
           @click="choose('房租账单')">房租账单</div>
      <div v-if="cooperLifeInfo.length == 0"
           @click="choose('生活账单')">生活账单</div>
      <div style="height:4px;background:#EFEFF4;"></div>
      <div style="color:#898989;"
           @click="show_addType = false">取消</div>
    </van-popup>
  </div>
</template>
<script>
import dayjs from 'dayjs';
import { GetBusinessOrderroom, GetBusinessLifeCostBillByorderroom, GetBusinessRentalBillByorderroom, GetBusinessRenewalContractLastByOrderRoom } from '@/api/api_business_hotel_and_scale';
import { GetBusinessCooperLifeCostBillBycooperorderroom, GetBusinessCooperRentalBillBycooperorderroom, GetBusinessCooperRenewalContractLastByCooperOrderRoom } from '@/api/api_two_landlord';
import { GetBusinessCooperOrderroomByroom } from '@/api/api_business';
export default {
    data() {
        return {
            NowInfo: [],
            rentInfo: {},
            roomInfo: [],
            cooperLifeInfo: [],
            allInfo: [],
            lastEndTime: '',
            show_addType: false
        };
    },
    filters: {
        dataFormat(val, type = false) {
            let str = type ? dayjs(val).format('YYYY年MM月DD') : dayjs(val).format('YYYY-MM-DD');
            return str;
        }
    },
    created() {
        this.$store.commit('setHeader', this.$route.query.cooperationName ? '租赁信息-' + this.$route.query.cooperationName : '租赁信息');
        this.getDetails();
    },
    methods: {
        getDetails() {
            GetBusinessCooperOrderroomByroom({ roomId: this.$route.query.roomId }).done((res) => {
                this.getCooperLast();
                this.NowInfo = res.data;
            });
        },
        getCooperLast() {
            GetBusinessCooperRenewalContractLastByCooperOrderRoom({ cooperOrderRoomId: this.$route.query.cooperOrderRoomId }).done((res) => {
                this.getCooperLifeInfo();
            });
        },
        getCooperLifeInfo() {
            GetBusinessCooperLifeCostBillBycooperorderroom({ cooperOrderRoomId: this.$route.query.cooperOrderRoomId }).done((res) => {
                this.cooperLifeInfo = res.data;
                this.getCooperRoomInfo();
                for (let i = 0; i < this.cooperLifeInfo.length; i++) {
                    let obj = {
                        diff: this.difffDay(this.cooperLifeInfo[i].endTime),
                        new_endTime: this.cooperLifeInfo[i].endTime,
                        set_round: i,
                        set_life: true,
                        set_length: this.cooperLifeInfo.length
                    };
                    Object.assign(this.cooperLifeInfo[i], obj);
                }
            });
        },
        getCooperRoomInfo() {
            GetBusinessCooperRentalBillBycooperorderroom({ cooperOrderRoomId: this.$route.query.cooperOrderRoomId }).done((res) => {
                this.cooperRoomInfo = res.data;
                for (let i = 0; i < this.cooperRoomInfo.length; i++) {
                    this.cooperRoomInfo[i].new_endTime = this.lastDay(this.cooperRoomInfo[i].startTime);
                    this.cooperRoomInfo[i].diff = this.difffDay(this.cooperRoomInfo[i].new_endTime);
                }
                this.allInfo = this.cooperRoomInfo.concat(this.cooperLifeInfo);
                let payStatusAll = [];
                let noPayStatusAll = [];
                let dateAll = [];
                this.allInfo.map((item) => {
                    if (item.payStatus == '1') {
                        payStatusAll.push(item);
                    } else {
                        noPayStatusAll.push(item);
                    }
                });
                payStatusAll.sort((a, b) => {
                    return Date.parse(b.new_endTime) - Date.parse(a.new_endTime);
                });
                noPayStatusAll.sort((a, b) => {
                    return b.diff > a.diff ? 1 : -1;
                });
                dateAll = noPayStatusAll.concat(payStatusAll);
                this.allInfo = dateAll;
            });
        },
        getCurrentInfo(id) {
            GetBusinessOrderroom(id).done((res) => {
                this.NowInfo = res.data;
                this.getinfo_life(id);
            });
        },
        getinfo_life(id) {
            GetBusinessLifeCostBillByorderroom(id).done((res) => {
                this.lifeInfo = res.data;
                this.getinfo_room(id);
                for (let i = 0; i < this.lifeInfo.length; i++) {
                    let obj = {
                        diff: this.difffDay(this.lifeInfo[i].endTime),
                        new_endTime: this.lifeInfo[i].endTime,
                        set_round: i,
                        set_life: true,
                        set_length: this.lifeInfo.length
                    };
                    Object.assign(this.lifeInfo[i], obj);
                }
            });
        },
        getinfo_room(id) {
            GetBusinessRentalBillByorderroom(id).done((res) => {
                this.getLast(id);
                this.roomInfo = res.data;
                for (let i = 0; i < this.roomInfo.length; i++) {
                    this.roomInfo[i].new_endTime = this.lastDay(this.roomInfo[i].startTime);
                    this.roomInfo[i].diff = this.difffDay(this.roomInfo[i].new_endTime);
                }
                this.allInfo = this.roomInfo.concat(this.lifeInfo);
                let payStatusAll = [];
                let noPayStatusAll = [];
                let dateAll = [];
                this.allInfo.map((item) => {
                    if (item.payStatus == '1') {
                        payStatusAll.push(item);
                    } else {
                        noPayStatusAll.push(item);
                    }
                });
                payStatusAll.sort((a, b) => {
                    return Date.parse(b.new_endTime) - Date.parse(a.new_endTime);
                });
                noPayStatusAll.sort((a, b) => {
                    return b.diff > a.diff ? 1 : -1;
                });
                dateAll = noPayStatusAll.concat(payStatusAll);
                this.allInfo = dateAll;
            });
        },
        getLast(id) {
            GetBusinessRenewalContractLastByOrderRoom(id).done((res) => {
                if (res.data) {
                    this.lastEndTime = res.data.endTime ? res.data.endTime.substr(0, 10) : '';
                }
            });
        },
        lastDay(time) {
            const days = dayjs();
            let last_sTime = dayjs(time).subtract(1, 'day');
            return last_sTime.format('YYYY-MM-DD');
        },
        difffDay(time) {
            const days = dayjs();
            let nowTime = days.format('YYYY-MM-DD');
            let diff = dayjs(time).diff(dayjs(nowTime), 'day');
            return diff;
        },
        goDetail() {
            this.$router.push({
                path: '/details_two',
                query: {
                    cooperOrderRoomId: this.NowInfo.id,
                    cooperationName: this.$route.query.cooperationName
                }
            });
        },
        goRoom(item) {
            if (!item.set_life) {
                if (!this.$route.query.cooperOrderId) {
                    this.$router.push({
                        path: '/roomFeeD',
                        query: {
                            rentalBillId: item.id,
                            orderType: this.NowInfo.orderType,
                            manual: item.manual
                        }
                    });
                } else {
                    this.$router.push({
                        path: '/business-room-details',
                        query: {
                            cooperOrderId: this.$route.query.cooperOrderId,
                            cooperRentalBillId: item.id,
                            set_length: item.set_length,
                            set_round: item.set_round
                        }
                    });
                }
            } else {
                localStorage.removeItem('life_fee');
                let query = {};
                if (this.$route.query.cooperOrderId) {
                    query = {
                        cooperLifeCostBillId: item.id,
                        set_length: item.set_length,
                        set_round: item.set_round
                    };
                } else {
                    query = {
                        lifeCostBillId: item.id,
                        set_length: item.set_length,
                        set_round: item.set_round
                    };
                }
                this.$router.push({
                    path: '/lifeFeetrans',
                    query: query
                });
            }
        },
        paySetting(item) {
            if (item.status == 0 && item.set_round == 0 && this.$route.query.cooperOrderId) {
                this.$router.push({
                    path: '/business-pay-set',
                    query: {
                        cooperOrderId: item.cooperOrderId,
                        cooperLifeCostBillId: item.id,
                        cooperOrderRoomId: this.$route.query.cooperOrderRoomId
                    }
                });
            }
        },
        choose(type) {
            this.$router.push({
                path: '/add_bill_two',
                query: {
                    title: type,
                    cooperOrderId: this.NowInfo.cooperOrderId,
                    cooperOrderRoomId: this.NowInfo.id,
                    startTime: this.NowInfo.cooperOrder.startTime,
                    endTime: this.NowInfo.cooperOrder.endTime
                }
            });
            this.show_addType = false;
        }
    }
};
</script>
<style scoped>
.c_88 {
    color: #888;
    font-weight: normal;
}
.line {
    line-height: 18px;
}
.c_f0 {
    color: #f00;
}
.f12 {
    font-size: 0.6rem;
}
.w16 {
    display: inline-block;
    width: 48px;
    font-size: 0.6rem;
    text-align: center;
    color: #fff;
    border-radius: 5px;
    margin-left: 6px;
    line-height: 18px;
}
.payed {
    color: #888;
}
.headUrl {
    display: flex;
    padding: 10px 14px;
    background: #fff;
    border-bottom: 1px solid #f0f0f0;
    margin-bottom: 20px;
}
.headUrl > img {
    width: 35px;
    height: 35px;
    margin-right: 14px;
    border-radius: 20px;
}
.headUrl > .headUrl_fo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.headUrl > .headUrl_fo > .name {
    text-align: left;
    font-size: 0.7rem;
    color: #000;
    font-weight: bold;
    width: 65%;
}
.headUrl > .headUrl_fo > .img_right {
    text-align: right;
    line-height: 20px;
    display: flex;
    align-items: center;
}
.headUrl > .headUrl_fo > .img_right > img {
    height: 13px;
    padding-left: 5px;
}
#roomBox {
    margin: 6px;
}
#roomBox > .room_fo > .loop {
    display: flex;
    justify-content: space-between;
    text-align: left;
    font-size: 0.8rem;
    align-items: center;
    line-height: 22px;
    margin: 10px 0;
    background: #fff;
    padding: 4px 8px;
    border-radius: 5px;
    height: 50px;
}
#roomBox > .room_fo > .loop > .money {
    min-width: 20%;
    text-align: right;
}
.loop_l {
    height: 100%;
    width: 90%;
    display: flex;
    justify-content: space-between;
}
.ispayed {
    height: 80%;
    margin: auto 0;
}
.ffa {
    border: 1px solid #ffab84;
    color: #ffab84;
}
.ff7 {
    border: 1px solid #ff7d41;
    color: #ff7d41;
}
.edf {
    border: 1px solid #8edfbc;
    color: #8edfbc;
}
.bc {
    border: 1px solid #02bc6c;
    color: #02bc6c;
}
.add_btn {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ff7f47;
    border-radius: 4px;
    background: #fff;
    line-height: 50px;
    margin: 0 6px;
}
.type_bottom {
    line-height: 40px;
    color: #000;
    border-bottom: 1px solid #efeff4;
}
.bb {
    border-bottom: 1px solid #efeff4;
}
</style>



